import React from "react";
import EPG, { Channel, TimeLine, TimeSlot, Show } from 'react-epg';
import useTVChannels from "./TVChannelsContextHook";


const MyEPG: React.FC = () => {
  const { isEPGOpen,channels }=useTVChannels();
  if (!isEPGOpen) return (<></>);
  return (
    <div className="epg" style={{ position:"absolute", top:"5vh",left:"5vw",height: "90vh", width: "90vw",overflow: "auto" }}>      
    <EPG>
    {channels.getAllChannels().filter((c)=>c.programmes.programs.length>0).map((channel) =>( 
      <TimeLine key={"TL"+channel.id} channel={<Channel name={channel.name} />}>
        {channel.programmes.programs.map((program,pidx)=>(
          <TimeSlot key={"TS"+channel.id+pidx} start={program.start} end={program.stop}>
            <Show key={"S"+channel.id+pidx} title={program.getProgramInfo().title} />
          </TimeSlot>
        ))}
        {/* {channel.programmes.programs.length===0 && (
          <TimeSlot start={new Date()} end={new Date()}>
            <Show/>
          </TimeSlot>
        )} */}
      </TimeLine>
      ))}
    </EPG>

    </div>
  );
}
export default MyEPG;